@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600&display=swap);
/*===== GOGGLE FONTS =====*/

/*=== Colores ===*/
:root {
  --first-color: #7d10c5;
  --dark-color: rgb(55, 25, 75);
  --dark-colorB: rgb(159, 149, 196);
  --white-color: #9b10c5;
}

/*=== Fuente y tipografia ===*/
:root {
  --body-font: "Open Sans", sans-serif;
  --biggest-font-size: 3.5rem;
  --h2-font-size: 1.25rem;
  --normal-font-size: 0.938rem;
}

@media screen and (min-width: 768px) {
  :root {
    --biggest-font-size: 7rem;
    --h2-font-size: 1.5rem;
    --normal-font-size: 1rem;
  }
}

/*=== z index ===*/
:root {
  --z-back: -10;
  --z-normal: 1;
  --z-tooltip: 10;
  --z-fixed: 100;
  --z-modal: 1000;
}

/*===== BASE =====*/
*,
::before,
::after {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  font-family: "Open Sans", sans-serif;
  font-family: var(--body-font);
}

h1,
h2,
h3,
p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

/*===== LAYOUT =====*/
.bd-grid {
  max-width: 1200px;
  margin-left: 1rem;
  margin-right: 1rem;
}

.l-header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgb(159, 149, 196);
  background-color: var(--dark-colorB);
  z-index: 1000;
  z-index: var(--z-modal);
}

/*===== NAVBAR =====*/
.nav {
  height: 3rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: bold;
}

@media screen and (max-width: 768px) {
  .nav__menu {
    position: fixed;
    left: 0%;
    top: -100%;
    background-color: rgba(0, 0, 0, 0.9);
    width: 100%;
    height: 40%;
    padding: 2rem;
    text-align: center;
    z-index: 100;
    z-index: var(--z-fixed);
    transition: 0.3s;
  }
}

.nav__logo {
  color: #9b10c5;
  color: var(--white-color);
}

.nav__item {
  margin-bottom: 2rem;
}

.nav__link {
  color: #9b10c5;
  color: var(--white-color);
}

.nav__toggle {
  font-size: 1.5rem;
  color: #9b10c5;
  color: var(--white-color);
  cursor: pointer;
}

/*=== Show menu ===*/
.show {
  top: 3rem;
}

/*===== HOME =====*/
.home {
  height: 100vh;
  overflow: hidden;
  position: relative;
}

.home__parallax {
  width: 100%;
  height: 100%;
  position: absolute;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.home__parallax-img1 {
  background-image: url(//RepoHV/static/media/imgm1.ae6ba710.png);
  z-index: 1;
  z-index: var(--z-normal);
}

.home__parallax-img2 {
  background-image: url(//RepoHV/static/media/imgm2.a785e660.png);
  z-index: 10;
  z-index: var(--z-tooltip);
}

.home__parallax-img3 {
  background-image: url(//RepoHV/static/media/imgm3.2fd9bb54.png);
  z-index: 100;
  z-index: var(--z-fixed);
}

.home__parallax-img4 {
  background-image: url(//RepoHV/static/media/imgm4.d0aa2f0d.png);
  z-index: 100;
  z-index: var(--z-fixed);
}

.home__title,
.home__subtitle {
  position: absolute;
  width: 100%;
  display: flex;
  justify-content: center;
  color: #9b10c5;
  color: var(--white-color);
}

.home__title {
  top: 32%;
  font-size: 3.5rem;
  font-size: var(--biggest-font-size);
  z-index: 1;
  z-index: var(--z-normal);
}

.home__subtitle {
  top: 44%;
  font-size: 1.25rem;
  font-size: var(--h2-font-size);
  font-weight: bold;
  z-index: 10;
  z-index: var(--z-tooltip);
}

.home__scroll {
  position: absolute;
  bottom: 2.5rem;
  width: 100%;
  display: flex;
  justify-content: center;
  font-size: 1.5rem;
  z-index: 1000;
  z-index: var(--z-modal);
}

.home__scroll .bx-mouse {
  color: #9b10c5;
  color: var(--white-color);
}

/*===== SECTION =====*/
.l-section {
  background-color: rgb(55, 25, 75);
  background-color: var(--dark-color);
}
.l-section2 {
  background-color: rgb(159, 149, 196);
  background-color: var(--dark-colorB);
}

.section {
  height: 100vh;
  max-width: 950px;
  margin-left: 1rem;
  margin-right: 1rem;
  padding: 2rem 0;
  color: #9b10c5;
  color: var(--white-color);
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

.section__data {
  margin-bottom: 3rem;
}

.section__title {
  font-size: 1.25rem;
  font-size: var(--h2-font-size);
  color: #7d10c5;
  color: var(--first-color);
  margin-bottom: 1rem;
}
.section__foto {
  width: 250px;
  height: 400px;

  -webkit-filter: drop-shadow(10px 16px 30px rgb(108, 44, 110)) contrast(130%);

          filter: drop-shadow(10px 16px 30px rgb(108, 44, 110)) contrast(130%);
  border-radius: 0.5rem;
}

.section__img {
  width: 285px;
  -webkit-filter: brightness(0.4);
          filter: brightness(0.4);
  border-radius: 10rem;
}

.section__err {
  width: 600px;

  border-radius: 10rem;
}

.link {
  border: solid rgb(185, 14, 207);
  padding: 5px;
  border-radius: 3rem;
  position: absolute;
  margin-left: 20px;
  color: #c00de4;
}

.linkR {
  border: solid rgb(185, 14, 207);
  padding: 5px;
  border-radius: 3rem;

  margin-left: 200px;
  color: #c00de4;
}

/*===== MEDIA QUERIES =====*/
@media screen and (min-width: 760px) {
  .nav {
    height: 4rem;
  }
  .nav__list {
    display: flex;
  }
  .nav__item {
    margin-right: 3rem;
    margin-bottom: 0;
  }
  .nav__toggle {
    display: none;
  }
  .home__title {
    top: 27%;
  }
  .home__subtitle {
    top: 50%;
  }
  .section {
    flex-direction: row;
    margin-left: auto;
    margin-right: auto;
  }
  .section__text {
    padding-right: 4rem;
  }
  .section__img {
    width: 470px;
  }
}

@media screen and (min-width: 1200px) {
  .bd-grid {
    margin-left: auto;
    margin-right: auto;
  }
}

